import React from "react";

function Favourites({ name, author, price, id, image, handleFavouritesClick }) {
  return (
    <div className="favourite_main">
      {/* <h1>Favourites</h1> */}
      <div className="fav_details">
        <div className="fav_details_main">
          <img
            src={image}
            alt={id}
            style={{
              width: "200px",
              height: "200px"
            }}
          />
          <div className="fav_details_main_2">
            <div className="fav_details_name">
              <div className="fav_details_item">
                <strong>Name</strong>: {name}
              </div>
              <div className="fav_details_item">
                <strong>Author</strong>:{author}
              </div>
              <div className="fav_details_item">
                <strong>Price</strong>: ${price}
              </div>
            </div>
            <div className="fav_details_fav mb-2">
              <button
                className="btn btn-outline-primary"
                onClick={handleFavouritesClick}
              >
                Remove from favorite
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Favourites;
