import React from "react";
import "../Styles/homepage.css";
import { Link } from "react-router-dom";

function HomePage() {
  return (
    <div className="homepage">
      <h1>Home Page</h1>
      <Link to="/home" className="booklist_link">
        <div className="container">
          <div className="py-4">
            <h3>Book List</h3>
          </div>
        </div>
      </Link>
    </div>
  );
}

export default HomePage;
