import "./styles.css";
import "../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import HomePage from "./Component/Pages/HomePage";
import BookDetails from "./Component/Pages/BookDetails";
import Favourites from "./Component/Pages/Favourites";
import NavBar from "./Component/NavBar";
import PaginateTable from "./Component/Pages/PaginationTable";

export default function App() {
  return (
    <div className="App">
      <Router>
        <NavBar />
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route exact path="/home" component={PaginateTable} />
          <Route exact path="/home/:id/detail" component={BookDetails} />
          <Route exact path="/home/favorites" component={Favourites} />
        </Switch>
      </Router>
    </div>
  );
}
