import React, { useEffect, useState } from "react";
import "../Styles/bookdetails.css";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import Favourites from "./Favourites";

function BookDetails() {
  const [books, setBooks] = useState([]);
  const [favourites, setFavourites] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    const bookFavourites = JSON.parse(
      localStorage.getItem("react-book-app-favourites")
    );

    if (bookFavourites) {
      setFavourites(bookFavourites);
    }
  }, []);

  const saveToLocalStorage = (items) => {
    localStorage.setItem("react-book-app-favourites", JSON.stringify(items));
  };

  const addFavouriteBook = (book) => {
    const newFavouriteList = [...favourites, book];
    setFavourites(newFavouriteList);
    saveToLocalStorage(newFavouriteList);
  };

  const removeFavouriteBook = (book) => {
    const newFavouriteList = favourites.filter(
      (favourite) => favourite.id !== book.id
    );

    setFavourites(newFavouriteList);
    saveToLocalStorage(newFavouriteList);
  };

  useEffect(() => {
    loadbooks();
  }, []);

  const loadbooks = async () => {
    const result = await axios.get(
      `https://609cd6ba04bffa001792d638.mockapi.io/books`
    );
    setBooks(result.data);
  };

  return (
    <div className="bookdetails">
      <div className="booklist_header">
        <Link to="/home" className="back_link">
          <h2>{`< Back`}</h2>
        </Link>
        <h1>Book Details</h1>
      </div>
      {books
        .filter((item) => item.id === id)
        .map((book) => {
          return (
            <div className="book_details">
              <div className="book_details_main">
                <img
                  src={book.book_image}
                  alt={book.id}
                  style={{
                    width: "450px",
                    height: "450px"
                  }}
                />
                <div className="book_details_main_2">
                  <div className="book_details_name">
                    <div className="book_details_item">
                      <strong>Name</strong>: {book.name}
                    </div>
                    <div className="book_details_item">
                      <strong>Author</strong>:{book.book_author}
                    </div>
                    <div className="book_details_item">
                      <strong>Price</strong>: ${book.price}
                    </div>
                  </div>
                  <div className="book_details_fav mb-2">
                    <button
                      className="btn btn-outline-primary"
                      onClick={() => addFavouriteBook(book)}
                    >
                      Add to favorite
                    </button>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      <h1>Favourites</h1>
      <div className="favourite_main">
        {favourites.map((fav) => {
          return (
            <Favourites
              // books={favourites}
              name={fav.name}
              author={fav.book_author}
              price={fav.price}
              image={fav.book_image}
              id={fav.id}
              handleFavouritesClick={removeFavouriteBook}
            />
          );
        })}
      </div>
    </div>
  );
}

export default BookDetails;
